@tailwind base;
@tailwind components;
@tailwind utilities;


.move-up {
    animation: MoveUp .1s ease-in-out;
}

@keyframes MoveUp {
    0% { transform: translateY(200px); }
    100% { transform: translateY(0); }
}

body {

    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
    background-size: 400% 400%;
    animation: gradient 15s ease infinite;
    height: 100vh;
}


@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}


.element {
    display: block;
    overflow:hidden;
    white-space:nowrap;
}

.element.fist-text {
    animation: showup 7s;
}


.element.second-text {
    margin-left:-355px;
    animation: slidein 6s;
}


.element.third-text {
    margin-left:-355px;
    animation: slidein 12s;
}

@keyframes showup {
    0% {opacity:0;}
    20% {opacity:1;}
    80% {opacity:1;}
    100% {opacity:0;}
}

@keyframes slidein {
    0% { margin-left:-800px; }
    20% { margin-left:-800px; }
    35% { margin-left:4px; }
    100% { margin-left:4px; }
}

@keyframes reveal {
    0% {opacity:0;width:0px;}
    20% {opacity:1;width:0px;}
    30% {width:355px;}
    80% {opacity:1;}
    100% {opacity:0;width:355px;}
}

